import sizes from './sizes'
import chroma from 'chroma-js';

export default {
    root: {
        width: '20%',
        height: '25%',
        margin: '0 auto',
        display: 'inline-block',
        position: 'relative',
        cursor: 'pointer',
        marginBottom: '-4px',
        '&:hover svg': {
            color: 'white',
            transform: 'scale(1.5)'
        },
        [sizes.down('lg')]: {
            width: '25%',
            height: '20%'
        },
        [sizes.down('md')]: {
            width: '50%',
            height: '10%'
        },
        [sizes.down('xs')]: {
            width: '100%',
            height: '5%'
        },
    },
    boxContent: {
        position: 'absolute',
        left: '0px',
        bottom: '0px',
        padding: '10px',
        width: '100%',
        // color: 'rgba(0, 0, 0, 0.5)',
        color: props =>
            chroma(props.color).luminance() <= 0.08 ? 'white' : 'rgba(0, 0, 0, 0.6)',
        letterSpacing: '1px',
        textTransform: 'uppercase',
        fontSize: '12px',
        display: 'flex',
        justifyContent: 'space-between'
    },
    deleteIcon: {
        // color: 'rgba(0, 0, 0, 0.5)',
        color: props =>
            chroma(props.color).luminance() <= 0.08 ? 'white' : 'rgba(0, 0, 0, 0.6)',
        transition: 'all 0.3s ease-in-out'
    }
}